.swat-content{
  .swat-form{
    margin: 20px 0;
    form{
      width: 70%;
      margin: 0 auto;
      .js-form-item{
        input{
          width: 100%;
        }
      }
    }
  }
  .db-show{
    .module_body{
      &:before{
        background-image: url("../src/photo/background_repeating.jpg");
        background-repeat: repeat;
        opacity: 0.3;
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        border-radius: 30px;
      }
      position: relative;
      margin: 30px 0;
      padding: 10px;

      .feedback_content{
        display: flex;
        justify-content: space-around;
        word-break: break-word;
        .user-information{
          width: 25%;
          text-align: center;
          .user-avatar-photo{
            img{
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
            margin: 0 auto;
            border-radius: 50%;
            width: 220px;
            height: 220px;
            overflow: hidden;
          }
        }
        .feedback-information{
          width: 50%;
        }
        .contact-information{
          width: 25%;
        }
        .user-information, .feedback-information, .contact-information{
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }

      }
      .buttons{
        padding: 10px;
        margin-top: 20px;
        .btn{
          padding: 10px 20px;
          color: black;
          border-radius: 10px;
          margin-right: 15px;
        }
        .delete{
          background-color: #ff2727;
        }
        .edit{
          background-color: #f8ff5d;
        }
      }
    }
  }
}
@media screen and (max-width: 1000px) {
  .swat-content {
    .swat-form {
      form {
        width: 100%;
      }
    }
    .db-show {
      .module_body {
        .feedback_content {
          flex-direction: column;
          align-items: center;

          .user-information, .feedback-information, .contact-information {
            width: 100%;
            margin: 10px;
          }
        }
      }
    }
  }
}
